import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
	state: {
		userinfo: {}, //
		hasregister: false, // 
		haslogin: false, // 
		token: '',

		ifnote: false,  	//
		ifvoice: false, // 
		tishi: 0,   //:
		
		playing: false, //

		days:[],   // 
		
	},
	mutations: {
		//
		StartPlay(state) {
			state.playing = true
		},
		StopPlay(state){
			state.playing = false
		},
		//
		xlogin(state, provider) {
			state.haslogin = true
			state.userinfo = provider
			
			if (state.userinfo.img != '') {
				state.hasregister = true
			};
			uni.setStorage({
				key: 'userinfo',
				data: provider
			})
		},
		// 
		read_storage(state, provider) {
			state.userinfo = provider
		},
		// 
		zhuxiao(state) {
			state.userinfo = {}
			uni.removeStorage({
				key: 'userinfo',
				success:res => {
					uni.showToast({
						title: '',
						icon:'success'
					});
					uni.reLaunch({
						url:'/pages/login/phone_login'
					})
				}
			})
		},
		// 
		logout(state) {
			state.userinfo = {}
			uni.removeStorage({
				key: 'userinfo',
				success:res => {
					uni.showToast({
						title: '',
						icon:'success'
					})
					uni.reLaunch({
						url:'/pages/login/phone_login'
					})
				}
			})
		},
		//
		xprofile(state,provider) {
			state.userinfo = provider
		},
		// 
		xtoux(state, provider){
			let img = provider.img
			state.userinfo.img = img
			state.userinfo.imgpath = img
		},
		
		//
		addNote(state) {
			state.ifnote = !state.ifnote
		},
		//
		addVoice(state) {
			state.ifvoice = !state.ifvoice

		},
		// 
		hidentishi(state) {
			state.tishi = 1
		},

		addTask(state,task) {
			let push_id = task.id + 1
			state.taskList.push({'id': push_id, 'content': task.content})
		},

		// 
		updateprofile(state, provider){
			state.userinfo.userName = provider.newname
			state.userinfo.img = provider.newimg
		}

	},
	actions: {},
	modules: {
		
	}
})
export default store
